<template>
    <div>
        <canvas ref="qrCode" :width="options.size" :height="options.size"></canvas>
    </div>
</template>
<script>
import QRCode from "qrcode";
export default {
    props: {
        /**
         * 
         * @param {String} value 
         * @param {Number} size 
         * @param {String} level 
         * @param {String} bgColor 
         * @param {String} fgColor 
         * @param {Number} margin 
         * @param {Number} scale 
         * @param {Number} version 
         * @param {String} errorCorrectionLevel 
         * @param {String} logo logo
         * @param {Number} logoWidth logo
         * @param {Number} logoHeight logo
         * @param {String} text 
         * @param {String} font 
         * @param {String} fontColor 
         * @param {Number} lineWidth 
         * @param {String} lineColor 
         * @param {Boolean} showBoder 
         *
         * */
        option: {
            type: Object,
            default: () => {
                return {};
            },
        },
    },
    data() {
        return {
            options: {
                value: "dzs-qr-code",
                size: 200,
                level: "L",
                bgColor: "#ffffff",
                fgColor: "#000000",
                margin: 4,
                scale: 4,
                version: -1,
                errorCorrectionLevel: "M",
                logo: "",
                logoWidth: 40,
                logoHeight: 40,
                text: "",
                font: "bold 16px Arial",
                fontColor: "#000000",
                lineWidth: 2,
                lineColor: "#000000",
                showBoder: true,
            },
        };
    },
    watch: {
        option: {
            handler: function (val) {
                setTimeout(() => {
                    this.drawQrCode();
                }, 100);
            },
            deep: true,
            immediate: true,
        },
    },
    methods: {
        drawQrCode() {
            let canvas = this.$refs.qrCode;
            let option = {
                ...this.options,
                ...this.option,
            };
            QRCode.toCanvas(
                canvas,
                option.value,
                {
                    width: option.size,
                    height: option.size,
                    color: {
                        dark: option.fgColor,
                        light: option.bgColor,
                    },
                    margin: option.margin,
                    scale: option.scale,
                    version: option.version,
                    errorCorrectionLevel: option.errorCorrectionLevel,
                },
                function (error) {
                    if (error) console.error(error);
                    console.log("success!");
                }
            );
            // logo
            if (option.logo) {
                this.setLogo(canvas, option);
            }
            // 
            if (option.text) {
                this.setText(canvas, option);
            }
            // 
            if (option.showBoder) {
                this.setFrame(canvas, option);
            }
            // canvas 
            let img = canvas.toDataURL("image/png");
            this.$emit("qrCode", img);
        },
        /**
         * logo
         * @param {Object} canvas canvas
         * @param {Object} options 
         */
        setLogo(canvas, options) {
            let ctx = canvas.getContext("2d");
            let img = new Image();
            img.src = options.logo;
            console.log((options.size - options.logoWidth) / 2 , (options.size - options.logoHeight) / 2)
            img.onload = function () {
                ctx.drawImage(
                    img,
                    (options.size - options.logoWidth) / 2,
                    (options.size - options.logoHeight) / 2,
                    options.logoWidth,
                    options.logoHeight
                );
            };
        },
        /**
         * 
         * @param {Object} canvas canvas
         * @param {Object} options 
         */
        setText(canvas, options) {
            let ctx = canvas.getContext("2d");
            ctx.font = options.font;
            ctx.fillStyle = options.fontColor;
            ctx.textAlign = "center";
            ctx.textBaseline = "middle";
            ctx.fillText(options.text, options.size / 2, options.size - 20);
        },
        /**
         * 
         * @param {Object} canvas canvas
         * @param {Object} options 
         */
        setFrame(canvas, options) {
            let ctx = canvas.getContext("2d");
            ctx.beginPath();
            ctx.lineWidth = options.lineWidth;
            ctx.strokeStyle = options.lineColor;
            ctx.rect(0, 0, options.size, options.size);
            ctx.stroke();
        },
    },
};
</script>
<style lang="less" scoped>
</style>
